"use client";

import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";

export default function NewStoryPage() {
  const [title, setTitle] = useState("");
  const [tags, setTags] = useState("");
  const [body, setBody] = useState("");
  const [loading, setLoading] = useState(false);

  async function handlePublish() {
    setLoading(true);
    try {
      const res = await fetch("/api/stories", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          title,
          tags: tags.split(",").map((t) => t.trim()).filter(Boolean),
          body,
        }),
      });
      if (!res.ok) throw new Error("Failed to publish");
      setTitle("");
      setTags("");
      setBody("");
      alert("Published!");
    } catch (e) {
      alert((e as Error).message);
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="max-w-3xl space-y-4">
      <h1 className="text-2xl font-semibold">Write a new story</h1>
      <Input placeholder="Title" value={title} onChange={(e) => setTitle(e.target.value)} />
      <Input placeholder="Tags (comma separated)" value={tags} onChange={(e) => setTags(e.target.value)} />
      <Textarea placeholder="Start writing..." rows={14} value={body} onChange={(e) => setBody(e.target.value)} />
      <div className="flex gap-3">
        <Button variant="outline" disabled={loading}>Save draft</Button>
        <Button onClick={handlePublish} disabled={loading}>{loading ? "Publishing..." : "Publish"}</Button>
      </div>
    </div>
  );
}
